
.navbar {
    width: 100%;
    height: 80px;
    padding-left: 30px;
    padding-right: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* flex-direction: row; */
    /* color: #61dafb; */
    background-color: green;
}

.nav-logo {
    display: inline-block;
    width: 175px;
}

.navlinks {
    display: flex;
    list-style: none;
  }

.navlink {
    color: #a8dd17;
}

.nav-item a {
    display: inline-block;
    padding: 10px 15px;
    text-decoration: none;
    color: white;
  }

.nav-item:hover a {
    color: red;
}